//
// Header and Primary Navigation
// --------------------------------------------------

.site-header{
	@include rem(padding, 20px);
}

@include media(min-width $screen-md){
	.logged-in .site-header{
		//top: 32px;
	}
}

.nav-primary{
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
}

.primary-nav__logo{
	@include rem(height, 50px);
	@include rem(width, 260px);
}

.nav-links{
	list-style: none;
	margin: 0px;
	padding: 0px;
}

.menu-item{
	@include media(max-width $screen-md) {
		display: block;
		@include rem(padding, 5px 0px);
	}
	display: inline-block;
	@include rem(padding, 0px 10px);
}

.menu-item a{
	color: $brand-blue;
	@include rem(font-size, 18px);
	&:hover{
		color: $brand-yellow;
	}
}

.menu-item.current-menu-item a{
	color: $brand-yellow;
	&:hover{
		color: darken($brand-yellow, 5%);
	}
}

// Collapsabile Nav

.nav-primary__collapse{
	@include media(min-width $screen-md) {
		display: block;
	}
	@include media(max-width $screen-md) {
		display: none;
		width: 100%;
		border-top: 1px solid $border-gray;
		@include rem(padding-top, 10px);
		@include rem(margin-top, 10px);
	}
}

.nav-primary__toggle{
	@include media(min-width $screen-md) {
		display: none;
	}
	@include media(max-width $screen-md) {
		display: block;
	}
}

.nav-primary__toggle-icon{
	@include rem(height, 23px);
	@include rem(width, 30px);
}

.nav-primary__toggle-icon path{
	@include transition(fill .15s linear);
}

.nav-primary__toggle-icon:hover path{
	fill: $brand-yellow;
}




// Home nav
.site-header__home{
	@include rem(padding, 20px);
	left: 0px;
	position: absolute;
	top: 0px;
	width: 100%;
	z-index: $zIndex-nav;
}